.thunder-container {
    position: absolute;
    top: 210px;
    left: 215px;

}

.thunder-container .thunder1 {

    background-color: yellow;
    width: 15px;
    height: 30px;
    position: relative;
    left: 18px;
    top: -5px;
    -webkit-transform: skew(-45deg);
    -webkit-animation: flash 3s linear infinite;
    -moz-transform: skew(-45deg);
    -moz-animation: flash 3s linear infinite;
    -ms-transform: skew(-45deg);
    -ms-animation: flash 3s linear infinite;

}

.thunder-container .thunder2 {

    background-color: yellow;
    width: 15px;
    height: 40px;
    position: relative;
    top: -10px;
    left: 2px;
    -webkit-transform: skew(-45deg);
    -webkit-animation: flash 3s linear infinite;
    -moz-transform: skew(-45deg);
    -moz-animation: flash 3s linear infinite;
    -ms-transform: skew(-45deg);
    -ms-animation: flash 3s linear infinite;
}

@-webkit-keyframes flash {
    0% {
        background: none;

    }
    25% {
        background: none;

    }
    50% {
        background: none;
    }
    65% {
        background: none;
    }
    75% {
        background: none;
    }
    80% {
        background: yellow;
    }
    100% {
        background: none;
    }
}

@keyframes flash {
    0% {
        background: none;

    }
    25% {
        background: none;

    }
    50% {
